.App {
  position: relative;
  height:100vh;
  margin:0;
  justify-content: center;
}

.display-2 {
  display: flex;
  font-size: 30px;
  font-family: 'Lobster', cursive;
  align-items: center;
  justify-content: center;
  
}

.display-5{
	font-size: 20px;
	font-family: 'Lobster', cursive;
	display: block;
	justify-content: center;
	align-items: center;
	padding-right: 10px;
}

.display-4 {
    font-size:25px;
    font-family: 'Lobster', cursive;
}


@media screen and (max-width:1080px) {
	.display-2 {
		align-items: center;
  		justify-content: center;
		font-size: 30px;
	}
	.display-4 {
		font-size: 20px;
	}
}


@media screen and (max-width:720px) {
	.display-2 {
		align-items: center;
  		justify-content: center;
		font-size: 25px;
	  }

	  .display-4 {
		font-size: 20px;
	}
}

@media screen and (max-width:500px) {
  .display-2 {
	align-items: center;
  	justify-content: center;
    font-size: 20px;
  }

  .display-4 {
	font-size: 15px;
}

.display-5{
	font-size: 15px;
	font-family: 'Lobster', cursive;
	display: block;
	justify-content: center;
	align-items: center;
}
}

@media screen and (max-width:300px) {
	.display-2 {
		align-items: center;
  	justify-content: center;
		font-size: 15px;
	}

	.display-4 {
		font-size: 12px;
	}

}

@media screen and (max-width:250px) {
	.display-2 {
		align-items: center;
  		justify-content: center;
		font-size: 12px;
	}

	.display-4 {
		font-size: 10px;
	}
}
