#professional-summary {
    margin: 0;
    padding: 30px;
}

.prof-summary-content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.img_caption {
    color:white;
}

.logo1 {
    width: 90px;
    height: 90px;
    margin: 10px;
}

.logo1:hover{
    
    width: 100px;
    height: 100px;
}


@media screen and (max-width:720px) {
    .prof-summary-content {
        display: block;
        justify-content: center;
        align-items: center;
    }
    
}


@media screen and (max-width:500px) {
    .logo1 {
        width: 60px;
        height: 60px;
        margin: 10px;
    }
    
    .logo1:hover{
        width: 70px;
        height: 70px;
    
    }

    .prof-summary-content {
        display: block;
        justify-content: center;
        align-items: center;
    }
    
}


@media screen and (max-width:300px) {
    .logo1 {
        width: 50px;
        height: 50px;
        margin: 5px;
    }
    
    .logo1:hover{
        width: 65px;
        height: 65px;
    
    }

    .img_caption {
        font-size: 10px;
    }

    .prof-summary-content {
        display: block;
        justify-content: center;
        align-items: center;
    }
    
}

