#my_skills{
    background-color:salmon;
}

.skill_content {
    display: flex;
    justify-content: space-evenly;
}



.logo {
    width:100px;
    height: 100px;
    margin: 10px;
}

@media screen and (max-width:1080px) {
    .logo {
        width:100px;
        height: 100px;
        margin: 10px;
    }
	
}


@media screen and (max-width:720px) {
    .logo {
        width:80px;
        height: 80px;
        margin: 10px;
    }
	
}

@media screen and (max-width:500px) {
    .logo {
        width:50px;
        height: 50px;
        margin: 20px;
    }
	
}